import sprite from "@/img/sprite.svg";
import { IconTypes } from "@/types/common";

interface Props {
  iconName: IconTypes;
  className?: string;
  side?: number;
}

function Icon({ iconName, className, side = 20 }: Props) {
  return (
    <svg
      className={className}
      style={{ display: "block", width: side, height: side }}
    >
      <use href={sprite + "#" + iconName} />
    </svg>
  );
}

export default Icon;
